'use client';

import React, { useState, useEffect } from 'react';
import {
  // Employee functions
  addEmployee,
  getEmployees,
  deleteEmployee,
  updateEmployee,
  // Lead functions
  addLead,
  getLeads,
  deleteLead,
  updateLead,
  // Product functions
  addProduct,
  getProducts,
  deleteProduct,
  updateProduct,
  // Quotation functions
  getQuotations,
  deleteQuotation,
  // Other functions
  getLeadSources,
  deleteLeadSource,
  addLeadSource,
  getDepartments,
  deleteDepartment,
  addDepartment,
  getEmployeeRoles,
  deleteEmployeeRole,
  addEmployeeRole
} from '../lib/data';

type DataType = 'employees' | 'leads' | 'products' | 'quotations' | 'departments' | 'roles' | 'sources';

export default function ComprehensiveFirestoreTest() {
  const [activeTab, setActiveTab] = useState<DataType>('employees');
  const [data, setData] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const tabs = [
    { id: 'employees' as DataType, label: ' Employees', color: 'blue' },
    { id: 'leads' as DataType, label: ' Leads', color: 'green' },
    { id: 'products' as DataType, label: ' Products', color: 'purple' },
    { id: 'quotations' as DataType, label: ' Quotations', color: 'orange' },
    { id: 'departments' as DataType, label: ' Departments', color: 'indigo' },
    { id: 'roles' as DataType, label: ' Roles', color: 'pink' },
    { id: 'sources' as DataType, label: ' Sources', color: 'teal' }
  ];

  const loadData = async (type: DataType) => {
    try {
      setLoading(true);
      let result: any[] = [];
      
      switch (type) {
        case 'employees':
          result = await getEmployees();
          break;
        case 'leads':
          result = await getLeads();
          break;
        case 'products':
          result = await getProducts();
          break;
        case 'quotations':
          result = await getQuotations();
          break;
        case 'departments':
          result = await getDepartments();
          break;
        case 'roles':
          result = await getEmployeeRoles();
          break;
        case 'sources':
          result = await getLeadSources();
          break;
      }
      
      setData(result);
      setMessage(`Loaded ${result.length} ${type} from Firestore`);
    } catch (error) {
      setMessage(`Error loading ${type}: ${error}`);
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (item: any, type: DataType) => {
    const itemName = item.name || item.quotationNumber || item.id;
    if (!confirm(`Are you sure you want to delete "${itemName}"?`)) {
      return;
    }

    try {
      setLoading(true);
      setMessage(`Deleting ${itemName} from Firestore...`);
      
      switch (type) {
        case 'employees':
          await deleteEmployee(item.id);
          break;
        case 'leads':
          await deleteLead(item.id);
          break;
        case 'products':
          await deleteProduct(item.id);
          break;
        case 'quotations':
          await deleteQuotation(item.id);
          break;
        case 'departments':
          await deleteDepartment(item.id);
          break;
        case 'roles':
          await deleteEmployeeRole(item.id);
          break;
        case 'sources':
          await deleteLeadSource(item.id);
          break;
      }
      
      setMessage(` ${itemName} deleted successfully from Firestore!`);
      await loadData(type);
    } catch (error) {
      setMessage(` Error deleting ${itemName}: ${error}`);
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleAddSample = async (type: DataType) => {
    try {
      setLoading(true);
      setMessage(`Adding sample ${type.slice(0, -1)} to Firestore...`);
      
      switch (type) {
        case 'employees':
          await addEmployee({
            name: `Test Employee ${Date.now()}`,
            email: `test${Date.now()}@example.com`,
            phone: '1234567890',
            role: 'Developer',
            department: 'Engineering',
            address: 'Test Address'
          });
          break;
        case 'leads':
          await addLead({
            name: `Test Lead ${Date.now()}`,
            company: 'Test Company',
            email: `lead${Date.now()}@example.com`,
            phone: '1234567890',
            status: 'New',
            source: 'Website',
            products: [],
            notes: 'Test lead created for testing'
          });
          break;
        case 'products':
          await addProduct({
            name: `Test Product ${Date.now()}`,
            description: 'A test product for demonstration',
            price: 1000,
            gstRate: 18
          });
          break;
        case 'departments':
          await addDepartment(`Test Department ${Date.now()}`);
          break;
        case 'roles':
          await addEmployeeRole(`Test Role ${Date.now()}`);
          break;
        case 'sources':
          await addLeadSource(`Test Source ${Date.now()}`);
          break;
      }
      
      setMessage(` Sample ${type.slice(0, -1)} added successfully!`);
      await loadData(type);
    } catch (error) {
      setMessage(` Error adding sample ${type.slice(0, -1)}: ${error}`);
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadData(activeTab);
  }, [activeTab]);

  const renderItem = (item: any, type: DataType) => {
    const getItemTitle = () => {
      switch (type) {
        case 'employees':
          return item.name;
        case 'leads':
          return `${item.name} (${item.company})`;
        case 'products':
          return item.name;
        case 'quotations':
          return item.quotationNumber;
        case 'departments':
        case 'roles':
        case 'sources':
          return item.name;
        default:
          return item.name || item.id;
      }
    };

    const getItemDetails = () => {
      switch (type) {
        case 'employees':
          return (
            <div className="text-sm text-gray-600">
              <p> {item.email}</p>
              <p> {item.phone}</p>
              <p> {item.role} - {item.department}</p>
            </div>
          );
        case 'leads':
          return (
            <div className="text-sm text-gray-600">
              <p> {item.email}</p>
              <p> {item.phone}</p>
              <p> Status: {item.status}</p>
              <p> Source: {item.source}</p>
            </div>
          );
        case 'products':
          return (
            <div className="text-sm text-gray-600">
              <p> Price: {item.price?.toLocaleString()}</p>
              <p> GST: {item.gstRate}%</p>
              <p className="text-xs">{item.description}</p>
            </div>
          );
        case 'quotations':
          return (
            <div className="text-sm text-gray-600">
              <p> Date: {item.date}</p>
              <p> Total: {item.grandTotal?.toLocaleString()}</p>
              <p> Status: {item.status}</p>
            </div>
          );
        default:
          return (
            <div className="text-sm text-gray-600">
              <p>ID: {item.id}</p>
            </div>
          );
      }
    };

    return (
      <div key={item.id} className="bg-white border rounded-lg p-4 shadow-sm">
        <div className="flex justify-between items-start">
          <div className="flex-1">
            <h3 className="font-semibold text-lg">{getItemTitle()}</h3>
            {getItemDetails()}
            {item.createdAt && (
              <p className="text-xs text-gray-400 mt-2">
                Created: {new Date(item.createdAt).toLocaleString()}
              </p>
            )}
          </div>
          <div className="flex flex-col space-y-1 ml-4">
            <span className="bg-green-100 text-green-800 px-2 py-1 rounded text-xs text-center">
              Firestore
            </span>
            <button
              onClick={() => handleDelete(item, type)}
              disabled={loading}
              className="bg-red-500 text-white px-2 py-1 rounded text-xs hover:bg-red-600 disabled:opacity-50"
            >
              Delete
            </button>
          </div>
        </div>
      </div>
    );
  };

  const currentTab = tabs.find(tab => tab.id === activeTab);

  return (
    <div className="max-w-7xl mx-auto p-6 bg-white">
      <h1 className="text-3xl font-bold mb-6 text-center"> Comprehensive Firestore Test</h1>
      
      {/* Status Message */}
      <div className="mb-6 p-4 bg-gray-100 rounded-lg">
        <p className="text-sm">
          <strong>Status:</strong> {message || 'Ready to test Firestore operations'}
        </p>
        {loading && (
          <div className="mt-2">
            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-500 inline-block"></div>
            <span className="ml-2 text-sm">Loading...</span>
          </div>
        )}
      </div>

      {/* Tab Navigation */}
      <div className="flex flex-wrap gap-2 mb-6 border-b">
        {tabs.map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id)}
            className={`px-4 py-2 rounded-t-lg font-medium transition-colors ${
              activeTab === tab.id
                ? `bg-${tab.color}-500 text-white border-b-2 border-${tab.color}-500`
                : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
            }`}
          >
            {tab.label}
          </button>
        ))}
      </div>

      {/* Content Area */}
      <div className="mb-6">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold">
            {currentTab?.label} ({data.length})
          </h2>
          <div className="flex space-x-2">
            <button
              onClick={() => loadData(activeTab)}
              disabled={loading}
              className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 disabled:opacity-50"
            >
              Refresh
            </button>
            {['quotations'].indexOf(activeTab) === -1 && (
              <button
                onClick={() => handleAddSample(activeTab)}
                disabled={loading}
                className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600 disabled:opacity-50"
              >
                Add Sample
              </button>
            )}
          </div>
        </div>

        {/* Data Grid */}
        {data.length === 0 ? (
          <div className="text-center py-12 text-gray-500">
            <p>No {activeTab} found in Firestore</p>
            <p className="text-sm mt-2">
              {['quotations'].indexOf(activeTab) === -1 
                ? 'Click "Add Sample" to create test data'
                : 'Quotations need to be created through the leads workflow'
              }
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {data.map(item => renderItem(item, activeTab))}
          </div>
        )}
      </div>

      {/* Instructions */}
      <div className="bg-yellow-50 p-4 rounded-lg">
        <h3 className="text-lg font-semibold mb-2"> Testing Instructions</h3>
        <ol className="list-decimal list-inside space-y-1 text-sm">
          <li>Click on different tabs to view different data types</li>
          <li>Click "Add Sample" to create test data in Firestore</li>
          <li>Click "Delete" on any item to remove it from Firestore</li>
          <li>Click "Refresh" to reload data from Firestore</li>
          <li>Check Firebase Console to see real-time changes</li>
          <li>Test with multiple data types to verify all CRUD operations</li>
        </ol>
        
        <div className="mt-4 p-3 bg-green-100 rounded">
          <p className="text-sm font-medium text-green-800"> What This Tests:</p>
          <ul className="text-xs text-green-700 mt-1 list-disc list-inside">
            <li>Create operations (Add Sample)</li>
            <li>Read operations (Loading data)</li>
            <li>Delete operations (Delete buttons)</li>
            <li>Real-time data sync with Firestore</li>
            <li>Error handling and user feedback</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
